import React, { Component } from 'react';
import Book from './Book.js';
import BookWithoutPrice from './BookWithoutPrice.js';
import Basket from './Basket.js';
import AddBookForm from './AddBookForm.js';
import SortBook from './SortBook.js';
import About from './About.js';
import Nav from './Nav.js';
import '../css/style.css'
import {HashRouter, Route, Switch} from 'react-router-dom';

class App extends Component{
    constructor(props){
        super(props);
        this.state = {
          dataBook: props.dataBook,
          items: {},
          page: 1,
          inUse: false,
        };
        this.removeBasket = this.removeBasket.bind(this);
        this.addBook = this.addBook.bind(this);
        this.sortBook = this.sortBook.bind(this);
        this.paginClick = this.paginClick.bind(this);
        this.navController = this.navController.bind(this);
    }
  navController(e){
    this.setState({
        inUse: !this.state.inUse,
    })
    if(e.target.id!="nav"){
      this.setState({
        inUse: false,
    })
    }
  }
  addBook(book){
    let newState = this.state.dataBook.slice(0);
    newState.push(book);
    this.setState({dataBook:newState});
  }
  sortBook(f){
    let newState = this.state.dataBook.slice(0);
    newState.sort(function(a,b){
      if(f>0){
        return a.price - b.price;
      }
      if(f<0){
        return b.price - a.price;
      }
      if(f==0){
        return a.id - b.id;
      }
    });
    this.setState({dataBook:newState});
  }
  addBasket = (id) => {
    const items = Object.assign({}, this.state.items);
    items[id] = id in items ? items[id] + 1: 1;
    this.setState( {items} );
  }

  removeBasket(id){
    const items = {};

    for(let i in this.state.items){
      if( id != i ){
        items[i] = this.state.items[i];
      }
    }

    this.setState( {items} )
  }
  paginClick(e){
    this.setState({
      page: e.target.id
    })
  }
    render(){
      const booksOnPage = 6;
      let lastOnPage = this.state.page * booksOnPage;
      let firstOnPage = lastOnPage - booksOnPage;
      let currentBooks = this.state.dataBook.slice(firstOnPage, lastOnPage);

      let pageNumbers = [];
      for (let i = 1; i <= Math.ceil(this.state.dataBook.length / booksOnPage); i++) {
        pageNumbers.push(i);
      }

      let pagination = pageNumbers.map(number => (
        number == this.state.page ?
        <li key={number} id={number} className="page-select" onClick={this.paginClick}>
          {number}
        </li> 
        :
        <li key={number} id={number} className="page-noselect"onClick={this.paginClick}>
          {number}
        </li>
       ));
     const books = currentBooks.map( book => (
        book.price ? 
        <Book 
          key={book.id}
          id={book.id}
          title={book.title}
          author={book.author}
          img={book.img}
          price={book.price}
          handleAddBasket={this.addBasket}        
        /> :
        <BookWithoutPrice 
        key={book.id}
        id={book.id}
        title={book.title}
        author={book.author}
        img={book.img}
        price={book.price}
      />        
      ));
      
     return <HashRouter basename={process.env.PUBLIC_URL}><div onClick={this.navController} className="app">
       <Nav
       navController={this.navController}
       inUse={this.state.inUse}
       />
       <Basket
        items={this.state.items}
        dataBook={this.state.dataBook}
        handleRemoveBasket={this.removeBasket}
        />
       <Switch>
          <Route path="/addbook" render={()=><AddBookForm addBook={this.addBook}/>}/>
          <Route path="/about" component={About}/>
          <Route path="/" render={()=>
            <div>
          <SortBook sortBook={this.sortBook}/>
          <ul className="pagination">{pagination}</ul>
          {books}
          <ul className="pagination">{pagination}</ul>
            </div>
          }/>
      </Switch>
     </div></HashRouter>
    }
}
export default App;

