import React, { Component } from 'react';
class Nav extends Component{
    constructor(props){
        super(props)
        this.state ={
            inUse: this.props.inUse,
        }
    }
    render(){
        // return <div className="navbar"><button>Меню пользователя</button></div>
        if(!this.props.inUse){
            return <div className="navbar"><button id="nav" onClick={this.props.navController}>Меню пользователя</button></div>
        }
        if(this.props.inUse){
            return <div className="navbar">
                <button id="nav" onClick={this.props.navController} >Меню пользователя</button>
                <ul id="navul">
                    <li><a href="#/">Главная</a></li>
                    <li><a href="#/about">About</a></li>
                    <li><a href="#/addbook">Добавить книгу</a></li>
                </ul>
                </div>
        }
    }
}
export default Nav;